import React, { useState } from 'react';
import '../Where.css';
import { mainWindow } from '../../App'

const CutImage  = React.memo(
    (props)=>{
        let [selectSector, setSelectSector] = useState('');
        let {holderPart, event} = props
        return (
            <svg width={290} height={175} xmlns="http://www.w3.org/2000/svg">
                <g>
                    <rect fill="#fff" id="canvas_background" height={177} width={292} y={-1} x={-1} />
                    <g display="none" overflow="visible" y={0} x={0} height="100%" width="100%" id="canvasGrid">
                        <rect fill="url(#gridpattern)" strokeWidth={0} y={0} x={0} height="100%" width="100%" />
                    </g>
                </g>
                <g>
                    <rect onClick={()=>{setSelectSector(''); holderPart('', event)}} className='pointer-text' id="svg_1" height={51} width={268} y="7.173296" x="10.5" strokeWidth="1.5" stroke="#000000"  fill={selectSector===''?'red':'#ffffff'} />
                    <rect onClick={()=>{setSelectSector(' '); holderPart(' ', event)}} className='pointer-text' stroke="#000000" id="svg_2" height="51.000001" width={268} y="62.173296" x="10.5" strokeWidth="1.5"  fill={selectSector===' '?'red':'#ffffff'} />
                    <rect onClick={()=>{setSelectSector('VIP '); holderPart('VIP ', event)}} className='pointer-text' id="svg_3" height={51} width={268} y="117.173296" x="10.5" strokeWidth="1.5" stroke="#000000"  fill={selectSector==='VIP '?'red':'#ffffff'} />
                    <text xmlSpace="preserve" textAnchor="start" fontFamily="Arvo, sans-serif" fontSize={24} id="svg_7" y="95.854403" x="82.343751" strokeWidth={0} stroke="#000" onClick={()=>{setSelectSector(' '); holderPart(' ', event)}} className='pointer-text' fill="#000000"> </text>
                    <text transform="matrix(1,0,0,1,0,0) " xmlSpace="preserve" textAnchor="start" fontFamily="Arvo, sans-serif" fontSize={24} id="svg_8" y="150.854403" x="88.984376" strokeWidth={0} stroke="#000000" onClick={()=>{setSelectSector('VIP '); holderPart('VIP ', event)}} className='pointer-text' fill="#000000">VIP </text>
                    <text xmlSpace="preserve" textAnchor="start" fontFamily="Arvo, sans-serif" fontSize={24} id="svg_9" y="40.854405" x="81.703126" strokeWidth={0} stroke="#000" fill="#000000" onClick={()=>{setSelectSector(''); holderPart('', event)}} className='pointer-text'></text>
                </g>
            </svg>
        )
    }
)

export default CutImage;
